@mixin header-title {
    font-family: $hero-title-font;
    font-weight: $hero-title-weight;
    margin-bottom: 0;
    text-transform: lowercase;
    line-height: 1;
    color: #fff;
    font-size: 5rem;
    text-align: center;

    @media screen and (min-width: 1250px){
        font-size: 8.4444444444rem;
    }

    @media screen and(min-width: 768px) {
        font-size: 7.7777777778rem;
    }

    @media screen and (min-width: 550px) {
        font-size: 6.3888888889rem;
    }

    @media screen and (max-width: 425px) {
        font-size: 2rem;
    }
}

@mixin float {
    box-shadow: 0 8px 17px 0 rgba(0,0,0,0.12),0 6px 20px 0 rgba(0,0,0,0.1);
    transition: box-shadow .2s ease-out,transform .2s ease-out,opacity .1s ease-out;
    transition-delay: 0s, 0s, 0s;
    transition-delay: .1s;

    &:hover, &:focus {
        opacity: 1 !important;
        box-shadow: 0 2px 5px 0 rgba(0,0,0,0.1),0 2px 10px 0 rgba(0,0,0,0.08);
        transform: translate(0,2px);
        z-index: 999;
    }
}