.footer {
  padding-top: 50px;
  padding-top: 2.7777777778rem;
  padding-bottom: 50px;
  padding-bottom: 2.7777777778rem;
  background-color: #005d8f; //background-image: url(/wp-content/themes/spearmex/dist/images/luxury-fishing-punta-mita-puerto-vallarta-mexico.jpg);
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
  color: #fff;
  a {
    color: #fff;
    &:hover,
    &:focus {
      color: #10abff;
      text-decoration: none;
    }
  }
  .logo-block {
    text-align: center; //padding-bottom: 1rem;
    padding: 2rem 0;
    /*background: url(/wp-content/themes/spearmex/dist/images/bottle.jpg) no-repeat bottom transparent;*/
    img.logo {
      max-width: 162px;
      height: 100%;
      width: 100%;
    }
  }
  .info-block {
    .up + .down {
      margin-top: 2rem;
    }
    .socials,
    .footer-links {
      list-style: none;
      padding: 0;
      margin: 0;
      li {
        display: inline-block;
      }
    }
    .newsletter {
      width: 100%;
      .mc4wp-form {
        .mc4wp-form-fields {
          display: -ms-flexbox;
          display: flex;
          -ms-flex-pack: center;
          justify-content: center;
          -ms-flex-align: center;
          align-items: center;
          -ms-flex-flow: row wrap;
          flex-flow: row wrap;
          width: 100%;
          @media screen and (max-width: 768px) {
            -ms-flex-pack: end !important;
            justify-content: flex-end !important;
            margin-right: 0;
            margin-left: 0;
            display: -ms-flexbox;
            display: flex;
            -ms-flex-wrap: wrap;
            flex-wrap: wrap;
          }
          label {
            -ms-flex: 0 1 20%;
            flex: 0 1 20%;
            max-width: 20%;
            line-height: 1.3;
            font-size: 14px;
            text-align: right;
            padding-right: 1rem;
            @media screen and (min-width: 769px) {
              margin-bottom: 0;
            }
            @media screen and (max-width: 768px) {
              max-width: 100% !important;
              text-align: center !important;
              flex: auto !important;
            }
          }
          input,
          a {
            flex: auto;
            max-width: 100%;
            &[type="email"] {
              @media screen and (max-width: 768px) {
                -ms-flex: 0 0 83.3333333333%;
                flex: 0 0 83.3333333333%;
                max-width: 83.3333333333%;
              }
            }
            &[type="submit"] {
              @media screen and (max-width: 768px) {
                margin-top: .5rem;
                margin-bottom: .5rem;
                -ms-flex: 0 0 16.6666666667%;
                flex: 0 0 16.6666666667%;
                max-width: 16.6666666667%;
              }
            }
          }
          [name="EMAIL"] {
            -ms-flex: 0 1 50%;
            flex: 0 1 50%;
            max-width: 50%;
            background-color: #fff;
            height: 50px;
            height: 3.0555555556rem;
            -webkit-appearance: none;
            -moz-appearance: none;
            -ms-appearance: none;
            -o-appearance: none;
            appearance: none;
            border-radius: 0;
            padding: 10px 25px;
            padding: .5555555556rem 1.3888888889rem;
            border: 0;
            box-sizing: border-box;
            color: #000;
            display: block;
            font-weight: 400;
            resize: none;
            vertical-align: middle;
            width: 100%;
          }
          [type="submit"] {
            display: block;
            -ms-flex: 0 1 20%;
            flex: 0 1 20%; //max-width: 20%;
            background-color: #fff;
            border: 1px solid #fff; //width: 100%;
            //min-width: 0;
            height: 3.0555555556rem;
            letter-spacing: 2px;
            margin-left: 2%;
            color: #fff;
            cursor: pointer;
            font-size: .80rem;
            @media screen and (max-width: 768px) {
              margin-left: 0;
            }
          }
        }
      }
    }
    .social-networks {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: center;
      justify-content: center;
      -ms-flex-align: center;
      align-items: center;
      -ms-flex-flow: row wrap;
      flex-flow: row wrap;
      width: 100%;
      font-size: 14px;
      &>p {
        -ms-flex: 0 1 130px;
        flex: 0 1 130px;
        margin: 0;
        margin-right: 2%;
        @media screen and (max-width: 768px) {
          margin: .5rem;
        }
      }
      .socials {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
        li {
          width: 1.6666666667rem;
          height: 1.6666666667rem;
          margin-right: .5555555556rem;
          &:last-child {
            margin-right: 0;
          }
          @media screen and (min-width: 768px) {
            width: 1.9444444444rem;
            height: 1.9444444444rem;
          }
          a {
            width: 100%;
            height: 100%;
            display: block;
            color: #fff;
            &:hover,
            &:focus {
              animation: bounce-scale 1s linear both;
            }
            svg {
              max-width: 100%;
              max-height: 100%;
              overflow: visible;
              fill: #fff;
            }
          }
        }
      }
    }
    .footer-links, .direction-footer p {
      font-size: 12px;
      font-size: .6666666667rem;
    }

    .footer-links {
      text-align: center;
      letter-spacing: 2px;
      li {
        margin-right: 13px;
        margin-left: 13px;
        position: relative;
        &:not(:last-child) {
          &:after {
            content: "|";
            position: absolute;
            top: 50%;
            right: -17px;
            transform: translateY(-50%);
          }
        }
      }
    }
  }
  .direction-footer {
    @media screen and (max-width: 768px) {
      width: 100% !important;
    }
  }
  .section-images-footer {
    //margin-bottom: 1.5rem;

    .title-images-footer  {
      margin-bottom: 1.5rem;
    }

    .img-footer {

      &:not(.imf-exclusive-vendor) {
        img.images-footer {
          max-height: 60px;
        }
      }

      &.imf-as-featured-in {
        img.images-footer {
          max-height: 45px;
        }
      }

      img.images-footer {
        padding: 8px;
        max-width: calc(100% / 2);
        max-height: 75px;
        height: 100%;
        width: 100%;
        align-self: center;
        object-fit: contain;

        @media screen and (max-width: 375px) {
          //max-height: 50px;
        }
      }
    }
  }
  @media screen and (max-width: 575px) {
    padding-bottom: 4.777778rem;
  }
}
