a {
    color: map-get($spearmex-palette, sixth);
}

.header.sticky-active .header-canopy .meta a {
    color: map-get($spearmex-palette, sixth);
    border-color: map-get($spearmex-palette, sixth);
}

.header-hero.hero-video {
    .bottom ul li a {
    color: map-get($spearmex-palette, primary);

    svg {
        fill: map-get($spearmex-palette, primary);
    }
    }
}

.dropdown-menu-mini-cart .widget_shopping_cart_content .buttons .button.wc-forward {
    background-color: map-get($spearmex-palette, primary);

    &:not(.checkout) {
    color: #222 !important;
    }
}

.dropdown-menu-mini-cart .widget_shopping_cart_content .buttons .button.checkout {
    background-color: map-get($spearmex-palette, fifth);
}

.woocommerce .products .product .star-rating,
.woocommerce #reviews .star-rating,
.woocommerce .woocommerce-product-rating .star-rating {
    color: map-get($spearmex-palette, primary);
}

.cart .quantity .minus,
.cart .quantity .qty,
.cart .quantity .plus {
    background-color: map-get($spearmex-palette, sixth);
}

.sidebar-menu .sidebar-container .sidebar-wrapper .sidebar-body ul.socials li a {
    color: map-get($spearmex-palette, sixth);

    svg {
    fill: map-get($spearmex-palette, sixth);
    }
}

blockquote.wp-block-quote {
    background-color: map-get($spearmex-palette, sixth);
    color: #fff;

    &:not(.is-large):not(.is-style-large) {
    border-left-color: map-get($spearmex-palette, fifth);
    }

    a {
    color: map-get($spearmex-palette, sixth);
    }
}

.wp-block-button {
    &.btn-see-more {
    .wp-block-button__link {
        background-color: map-get($spearmex-palette, secondary);
        color: #fff;
    }
    }
}

.hero {
    &.hero-primary {
    background-color: map-get($spearmex-palette, primary);

    .wp-block-button {
        &.btn-see-more {
        .wp-block-button__link {
            background-color: #fff;
            color: map-get($spearmex-palette, primary);
        }
        }
    }
    }

    &.hero-secondary {
    background-color: map-get($spearmex-palette, secondary);

    .wp-block-button {
        &.btn-see-more {
        .wp-block-button__link {
            background-color: #fff;
            color: map-get($spearmex-palette, secondary);
        }
        }
    }
    }

    &.hero-third {
    background-color: map-get($spearmex-palette, third);

    .wp-block-button {
        &.btn-see-more {
        .wp-block-button__link {
            background-color: #fff;
            color: map-get($spearmex-palette, third);
        }
        }
    }
    }

    &.hero-fourth {
    background-color: map-get($spearmex-palette, fourth);

    .wp-block-button {
        &.btn-see-more {
        .wp-block-button__link {
            background-color: #fff;
            color: map-get($spearmex-palette, fourth);
        }
        }
    }
    }

    &.hero-fifth {
    background-color: map-get($spearmex-palette, fifth);

    .wp-block-button {
        &.btn-see-more {
        .wp-block-button__link {
            background-color: #fff;
            color: map-get($spearmex-palette, fifth);
        }
        }
    }
    }

    &.hero-sixth {
    background-color: map-get($spearmex-palette, sixth);
    color: #fff;

    .wp-block-button {
        &.btn-see-more {
        .wp-block-button__link {
            background-color: #fff;
            color: map-get($spearmex-palette, sixth);
        }
        }
    }
    }
}

.wp-block-mkl-hero-block {
    &.has-pantone-108-c-background-color {
    background-color: $pantone-108c;
    }

    &.has-pantone-130-c-background-color {
    background-color: $pantone-130c;
    }

    &.has-pantone-1558-c-background-color {
    background-color: $pantone-1558c;
    }

    &.has-pantone-319-c-background-color {
    background-color: $pantone-319c;
    }

    &.has-pantone-298-c-background-color {
    background-color: $pantone-298c;
    }

    &.has-pantone-3005-c-background-color {
    background-color: $pantone-3005c;

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    .h1,
    .h2,
    .h3,
    .h4,
    .h5,
    .h6 {
        color: #fff;
    }
    }
}

.section-booking-movil {
    a.btn {
    &.btn-book-now {
        background-color: map-get($spearmex-palette, sixth);
        color: #fff;
    }

    &.btn-phone {
        background-color: map-get($spearmex-palette, primary);
        color: map-get($spearmex-palette, sixth);
    }
    }
}

.footer {
    background-color: map-get($spearmex-palette, sixth);

    a {
    color: #fff;
    }
}

.single {
    .post {
        header {
            background-color: map-get($spearmex-palette, sixth);

            a {
                color: #fff;
                &:hover, &:focus {
                    color: #fff;
                }
            }
        }
    }
}

.sidebar {
    .widget {
        & > .widget-title, & > h3 {
            background-color: map-get($spearmex-palette, sixth);
        }
    }
}