.sidebar {
    .widget {
        &:not(:last-child) {
            //margin-bottom: 1rem;
            padding-bottom: 1rem;
            //border-bottom: 1px solid #e2e2e2;
        }

        & > h3, &.widget-title {
            font-size: 1.18rem;
            margin-bottom: .75rem;

            background-color: #c4c6c7;
            color: #fff;
            text-align: center;
            display: block;
            padding: 1rem;
        }

        ul {
            list-style: none;
            padding: 0;
            margin: 0;

            li {
                display: block;
                padding: 0.25rem 0;

                &:before {
                    font-family: "Font Awesome 5 Free";
                    font-weight: 900;
                    content: "\f105";
                    display: inline-block;
                    vertical-align: middle;
                    margin-right: .5rem;
                    color: #525252;

                    font-style: normal;
                    font-variant: normal;
                    text-rendering: auto;
                    -webkit-font-smoothing: antialiased;
                }
            }
        }

        &.search-2, &.widget_search {
            @extend %search-style;

            .search-form {
                flex-wrap: nowrap;
                label {
                    width: 100%;
                    max-width: 276.05px;

                    input {
                        width: 100%;
                    }
                }
            }
        }
    }
}