// Color
$purple: #5f3770;
$pantone-108c: #fedb00;
$pantone-130c: #f2a900;
$pantone-1558c: #f57633;
$pantone-319c: #2dccd3;
$pantone-298c: #41b6e6;
$pantone-3005c: #0077c8;

$pantone-375c: #98C223;
$pantone-627c: #112821;
$pantone-1495c: #FF7A1D;

$green: #0a4529;
$blue-canopy: #005d8f;

// Grid settings
$main-sm-columns:       12;
$sidebar-sm-columns:    4;

// Fonts
$global-font: 'Proxima Nova', sans-serif, cursive;
$hero-title-font: 'Salsa', cursive;
$font-family-base: $global-font;
$hero-title-weight: 400;

// Search
$search-button-background-color: $purple;
$search-button-border-color: $purple;
$search-border-color: $purple;
$search-button-text-color: #fff;
$search-border-radio: 0;

// Palettes

$spearmex-palette: (
    primary: $pantone-108c,
    secondary: $pantone-130c,
    third: $pantone-1558c,
    fourth: $pantone-319c,
    fifth: $pantone-298c,
    sixth: $pantone-3005c
);

$canopy-palette: (
    /* primary: #f7a800,
    secondary: #c5a30c,
    third: $pantone-1558c,
    fourth: #5f3770,
    fifth: #005d8f,
    sixth: #0eb3d6, */
    primary: $pantone-375c,
    secondary: $pantone-627c,
    third: $pantone-1495c
);