// Grid system
.main {
  @include make-col-ready();
  @include media-breakpoint-up(sm) {
    @include make-col($main-sm-columns);
    .sidebar-primary & {
      @include make-col($main-sm-columns - $sidebar-sm-columns);
    }
  }
}
.sidebar {
  @include make-col-ready();
  @include media-breakpoint-up(sm) {
    @include make-col($sidebar-sm-columns);
  }
}

.wp-block-columns {
  margin-left: -15px;
  margin-right: -15px;
  .wp-block-column {
    padding-left: 15px;
    padding-right: 15px;
  }
  &.has-3-columns {
    .wp-block-column {
      max-width: calc(100% / 3);
      width: 100%;
      height: auto;
    }
  }
}

.hero {
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
  padding: 3rem 15px;
}