body.admin-bar {
  .header.sticky-active {
    .header-canopy {
      align-items: center;
      top: 32px;
    }
  }
}

.header {
  width: 100%;
  height: auto;
  z-index: 100;
  position: relative;
  background-color: transparent;
  &.sticky-top {
    position: -webkit-sticky !important;
    position: sticky !important;
    z-index: 1020 !important;
  }
  &.sticky-active {
    animation: slideInDown .5s ease;
    color: #005d8f;
    .header-canopy {
      background-color: #fff;
      padding-top: 10px;
      padding-top: .8965555556rem;
      padding-bottom: 10px;
      padding-bottom: .8965555556rem;
      @media screen and (max-width: 425px) {
        padding: 1rem;
      }
      .canopy-logo {
        width: 200px;
        .logo {
          display: none;
        }
        .logo_stuck {
          display: block;
        }
        @media screen and (max-width: 470px) {
          width: 160px;
          align-self: center;
        }
      }
      .meta {
        a {
          color: #005d8f;
          border-color: #005d8f;
        }
      }
    }
    #mega-menu-wrap-center-menu #mega-menu-center-menu>li.mega-menu-item>a.mega-menu-link {
      color: #005d8f !important;
    }
  }
  .header-canopy {
    display: flex;
    justify-content: space-between;
    padding: 1.1111111111rem 2.7777777778rem 0 1.9444444444rem;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    background-color: transparent;
    align-items: center;
    @media screen and (max-width: 425px) {
      padding: 1rem;
    }
    .canopy-logo {
      display: inline-table;
      margin: 0;
      padding: 0;
      width: 162px;
      height: auto;
      img {
        display: block;
        max-width: 100%;
        width: 100%;
        height: auto;
        &.logo_stuck {
          display: none;
        }
      }
    }
    .meta {
      height: fit-content;
      align-items: flex-start;
      &.go-booking {
        .btn-mini-cart {
          @media screen and (max-width: 425px) {
            display: none !important;
          }
        }
      }
      &.no-booking {
        .btn-book-now {
          @media screen and (max-width: 425px) {
            display: none !important;
          }
        }
      }
      .btn {
        color: #fff;
        border-color: #fff;
        text-transform: uppercase;
        font-weight: 100;
        font-size: 12px;
        padding: 1rem 1.1111111111rem;
        &:not(:last-child) {
          margin-right: .5555555556rem;
        }
        span {
          display: block;
        }
        &:hover {
          span {
            animation: bounce-scale 1s linear both;
          }
        }
      }
      .btn-phone {
        @media screen and (max-width: 590px) {
          //display: none;
        }
        i {
          padding-top: 6px;
        }
      }
    }
  }
}

.header-hero {
  svg {
    fill: #fff;
    overflow: visible;
    width: 2.7777777778rem;
    height: 2.7777777778rem;
  }
  &.hero-video {
    position: relative;
    overflow: hidden;
    color: #fff;
    background-color: #000;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 420px;
    min-height: 23.3333333333rem;
    height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-pack: center;
    justify-content: center;
    font-size: 1.1111111111rem;
    -ms-flex-flow: row;
    flex-flow: row;

    &:after {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, .2);
      z-index: 2;
    }
    #video-background {
      position: absolute;
      right: 0;
      bottom: 0;
      min-width: 100%;
      min-height: 100%;
      width: 100%; //auto; 
      height: auto;
      z-index: 1;
      object-fit: cover;
    }
    .wrap,
    .bottom {
      z-index: 3;
    }
    a {
      color: #fff;
    }
    .wrap {
      margin-left: 0;
      margin-right: 0;
      text-align: center;
      z-index: 10;
      p {
        font-family: 'Proxima Nova', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
        margin-top: -.2777777778rem;
        margin-bottom: 0;
        text-transform: uppercase;
        font-weight: 700;
        letter-spacing: 2px;
        line-height: 1.8;
        padding: 0.6rem 0;
        @media screen and (max-width: 425px) {
          font-size: .5rem;
        }
      }
      .button-play {
        outline: 0;
        display: block;
        &:hover {
          animation: bounce-scale 1s linear both;
        }
      }
    }
    .bottom {
      position: absolute;
      width: 100%;
      bottom: 0;
      left: 0;
      z-index: 10;
      color: #fff;
      font-size: 14px;
      line-height: 1;
      text-transform: uppercase;
      font-weight: 700;
      h4 {
        font-family: 'Proxima Nova', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
        text-align: center;
        margin-bottom: 10px;
        text-transform: uppercase;
        letter-spacing: 1px;
        font-size: .7777777778rem;
        font-weight: 700;
        line-height: 1.3;
        padding-left: 45px;
        padding-left: 2.5rem;
        padding-right: 45px;
        padding-right: 2.5rem;
        @media (min-width: 1250px) {
          padding-left: 0;
          padding-right: 0;
        }
        @media (min-width: 768px) {
          letter-spacing: 3px;
          margin-bottom: 1.1111111111rem;
        }
      }
      ul {
        background-color: rgba(0, 0, 0, .5);
        margin: 0;
        padding: 0;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-flow: row wrap;
        flex-flow: row wrap;
        -ms-flex-pack: center;
        justify-content: center;
        -ms-flex-align: center;
        align-items: center;
        svg {
          display: block;
          width: 32px;
          height: 32px;
          margin-right: 10px;
          fill: $pantone-1495c;
        }
        li {
          list-style-type: none;
          padding: 0;
          margin: 0;
          @media screen and (min-width: 960px) {
            border-right: 1px solid #38474d;
          }
          a {
            cursor: pointer;
            padding: 1.1111111111rem .8333333333rem;
            list-style-type: none;
            font-weight: 700;
            text-transform: uppercase;
            font-size: 15px;
            display: -ms-flexbox;
            display: flex;
            -ms-flex-flow: row wrap;
            flex-flow: row wrap;
            -ms-flex-align: center;
            align-items: center;
            -ms-flex-pack: center;
            justify-content: center;
            color: $pantone-1495c;
            @media (min-width: 960px) {
              padding: 1.1111111111rem 2.5rem;
            }
            @media (min-width: 768px) {
              padding: 1.1111111111rem .8333333333rem;
            }
            &:hover {
              text-decoration: none;
              svg {
                animation: bounce-scale 1s linear both;
              }
            }
            svg {
              will-change: transform;
            }
            span {
              display: none;
              letter-spacing: 1px;
              @media (min-width: 960px) {
                letter-spacing: 2px;
              }
              @media (min-width: 768px) {
                display: block;
              }
            }
          }
        }
      }
    }
    h1 {
      @include header-title();
    }
  }
}

.section-booking-movil {
  @media screen and (max-width: 575px) {
    position: fixed;
    left: 0;
    bottom: 0;
    color: white;
    text-align: center;
    z-index: 999;
    width: 100% !important;
		a {
      width: 100%;
      padding: .7rem .75rem;
      background-color: #98C223;
      border: solid 5px white;
		}
  }
}

body.sidebar-open {
  overflow: hidden !important;
}

.sidebar-menu {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
  margin-left: auto;
  transition: all .5s;
  &:not(.open),
  &.closed {
    width: 0px;
    overflow: hidden;
    transition: all .5s;
  }
  .sidebar-container {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: stretch;
    align-content: flex-start;
    .sidebar-image {
      height: 100%;
      width: 100%;
      background-color: rgba(0, 0, 0, .3);
      .booking-image-wrap {
        width: 100%;
        height: 100%;
        position: relative;
        overflow: hidden;
        img {
          position: absolute;
          width: 100%;
          height: 100%;
          object-fit: cover;
          top: 50%;
          left: 0;
          transform: translateY(-50%);
        }
      }
      @media screen and (max-width: 425px) {
        display: none;
      }
    }
    .sidebar-wrapper {
      max-width: 762px;
      width: 100%;
      height: 100%;
      overflow-y: auto;
      overflow-x: hidden;
      padding: 2.9090909091rem 1.4545454545rem;
      background-color: #fff;
      &.sidebar-size-menu {
        max-width: 440px;
      }
      @media screen and (min-width: 1440px) {
        padding: 4.0727272727rem 2.1818181818rem;
      }
      @media screen and (min-width: 1250px) {
        padding-right: 2.9090909091rem;
        padding-left: 2.9090909091rem;
        padding-top: 3.6363636364rem;
        padding-bottom: 3.6363636364rem;
      }
      @media screen and (min-width: 960px) {
        padding-right: 2.3272727273rem;
        padding-left: 2.3272727273rem;
      }
      @media screen and (min-width: 768px) {
        padding-top: 3.6363636364rem;
        padding-bottom: 3.6363636364rem;
      }
      .sidebar-header {
        display: flex;
        .sidebar-title {
          margin-bottom: 1rem;
        }
        .close {
          float: none;
          margin-left: auto;
          font-size: 3rem;
          font-weight: 400;
          line-height: 0.6;
          margin-top: -8px;
        }
      }
      .sidebar-body {
        .canopy-menu {
          padding-top: 20px;
          ul {
            list-style: none;
            padding: 0;
            margin: 0;
            li {
              display: block;
              margin: 0;
              &.open-submenu {
                .sub-menu {
                  height: auto;
                  visibility: visible;
                  transition: all .5s;
                  li {
                    animation: inFromRight .6s both cubic-bezier(.7, 0, .3, 1);
                  }
                }
              }
              .sub-menu {
                height: 0;
                visibility: hidden;
                overflow: hidden;
                margin-left: 5%;
                transition: all .5s;
              }
              &.menu-item-has-children {
                &>a:after {
                  content: "\203A";
                  display: block;
                  position: absolute;
                  width: 10px;
                  height: 10px;
                  top: 50%;
                  right: 10px;
                  line-height: 0;
                  font-size: 1.30rem;
                  color: #444;
                  transition: all .5s;
                }
              }
              a {
                text-transform: uppercase;
                font-weight: 400;
                overflow: hidden;
                letter-spacing: 1px;
                text-overflow: ellipsis;
                white-space: nowrap;
                font-size: .8333333333rem;
                transform: translateZ(0);
                will-change: transform, opacity;
                transition: transform .3s, opacity .3s;
                padding: 15px 0;
                padding: .8333333333rem 0;
                color: #000;
                position: relative;
                display: block;
                border-bottom: 1px solid #ccc;
                &:hover,
                &:focus {
                  text-decoration: none;
                  &,
                  &:after {
                    color: #005d8f;
                  }
                }
              }
            }
          }
        }
        .buttons-menu {
          margin-top: 2rem;
        }
        ul.socials {
          list-style: none;
          padding: 0;
          margin: 0;
          margin-top: 20px;
          margin-top: 1.1111111111rem;
          display: flex;
          flex-flow: row nowrap;
          justify-content: center;
          
          li {
            border: none;
            float: left;
            font-size: 18px;
            font-size: 1rem;
            padding: 0;
            margin: 0;
            margin-left: 10px;
            margin-left: .5555555556rem;
            width: 30px;
            height: 30px;
            &:first-child {
              margin-left: 0;
            }
            a {
              width: 100%;
              height: 100%;
              display: block;
              color: #005d8f;
              svg {
                width: 100%;
                height: 100%;
                fill: #005d8f;
              }
            }
          }
        }
        .adwards {
          border-top: 1px solid #ccc;
          padding-top: .8333333333rem;
          margin-top: 1.1111111111rem;
          text-align: center;

          .adward {
            display: inline-block;
            img {
              max-width: 200px;
              height: auto;
            }
          }
        }

        select {
          box-shadow: none !important;
          border-radius: 0;
          padding: 10px 25px;
          padding: .5555555556rem 1.3888888889rem;
          height: 50px;
          height: 2.7777777778rem;
          background-color: #f1f2f3;
          border: 0;
          box-sizing: border-box;
          color: #000;
          display: block;
          font-weight: 400;
          resize: none;
          vertical-align: middle;
          width: 100%;

          &[multiple] {
            height: 100px;
            height: 6rem;
          }

          &:hover, &:focus {
            transform: none;
          }
        }
      }
    }
  }
}

.header-cover {
  display: block;
  width: 100%;
  height: 350px;
  background-color: #ddd;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  .wrap {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 2;
  }
  .cover-title {
    h1 {
      @include header-title();
      margin-bottom: 1.4rem;
    }
  }
  &:after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .2);
    z-index: 1;
  }
}

.sticky-hold:not(.sticky-active) {
  .dropdown-back {
    color: #fff;
  }
}

.dropdown-back {
  text-decoration: none;
  padding: 6px;
  .basket-item-count {
    margin-left: 4px;
  }
}

.dropdown-menu-mini-cart {
  padding: .5rem;
  min-width: 350px; // Effect
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  max-height: 0;
  display: block;
  overflow: hidden;
  opacity: 0;
  &.show {
    max-height: max-content;
    opacity: 1;
  }
  .widget_shopping_cart_content {
    ul {
      list-style: none;
      padding: 0;
      li {
        position: relative;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: flex-start;
        align-items: center;
        .product-image {
          flex: 0 1 auto;
          justify-self: flex-start;
          position: relative;
          margin-right: 10px;
          max-width: 60px;
          img {
            width: 60px;
            height: auto;
            margin: 0;
            border-radius: 4px;
            float: none;
          }
        }
        &:not(:last-child) {
          padding-bottom: 6px;
          margin-bottom: 6px;
          border-bottom: 1px dotted #ddd;
        }
        &:hover {
          .remove {
            opacity: 1;
            transition: all .5s;
          }
        }
        .remove {
          display: block;
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          color: rgb(179, 18, 18);
          font-weight: 800;
          opacity: 0;
          transition: all .5s;
          padding: 10px;
          border-radius: 0;
          background-color: rgba(255, 255, 255, 0.7);
          line-height: 0.7;
          width: 100%;
          height: 100%;
          text-decoration: none;
          margin: auto;
          text-align: center;
          font-size: 1.7rem;
          &:hover {
            color: rgb(179, 18, 18);
          }
        }
        a:not(.remove) {
          text-decoration: none;
          color: #212529;
        }
        .quantity {
          flex: 0 1 auto;
          align-self: baseline;
          text-align: center;
          display: inline-block;
          vertical-align: middle;
          margin-left: auto;
          padding-left: 10px;
          font-size: .84rem;
          max-width: 67px;
          width: 100%;
          .amount {
            display: block;
          }
        }
      }
    }
    .total {
      margin: 1rem 0 .4rem 0;
      border-top: 1px solid #f5f5f5;
      padding: 10px 0;
      text-align: right;
    }
    .buttons {
      display: flex; //justify-content: space-between;
      margin-bottom: -.5rem;
      margin-left: -.5rem;
      margin-right: -.5rem;
      .button {
        padding: 14px 10px;
        color: #fff !important;
        text-transform: uppercase;
        border-radius: 0px;
        text-decoration: none;
        font-size: 0.84rem;
        width: 100%;
        text-align: center;
        &.wc-forward {
          background-color: $pantone-1495c;
        }
        &.checkout {
          background-color: $pantone-375c;
        }
      }
    }
  }
  @media screen and (max-width: 425px) {
    display: none;
  }
}

div#mega-menu-wrap-center-menu.mega-menu-wrap {
  flex-grow: 1 !important;
  ul#mega-menu-center-menu {
    display: flex;
    justify-content: flex-end;
    height: 100% !important;
    margin-right: 1rem;
    @media screen and (max-width: 990px) {
      display: none;
    }
    li {
      a.mega-menu-link {
        height: 100%;
        padding-top: 7px;
      }
      &.quit-responsive {
        @media screen and (max-width: 1196px) {
          display: none;
        }
      }
    }
  }
  div.mega-menu-toggle {
    display: none;
  }
}


#mega-menu-wrap-center-menu #mega-menu-center-menu > li.mega-menu-megamenu > ul.mega-sub-menu > li.mega-menu-item h4.mega-block-title, #mega-menu-wrap-center-menu #mega-menu-center-menu > li.mega-menu-megamenu > ul.mega-sub-menu li.mega-menu-column > ul.mega-sub-menu > li.mega-menu-item h4.mega-block-title{
  text-transform: unset !important;
  color: #112821;
}