// Search form
.search-form {
  @extend .form-inline;
}
.search-form label {
  font-weight: normal;
  @extend .form-group;
}
.search-form .search-field {
  @extend .form-control;
}
.search-form .search-submit {
  @extend .btn;
  @extend .btn-secondary;
}

select {
  padding: .45rem .87rem;
  padding-right: 2.4rem;
  //box-shadow: 0 8px 17px 0 rgba(0,0,0,0.12),0 6px 20px 0 rgba(0,0,0,0.1);
  //transition: box-shadow .2s ease-out,transform .2s ease-out,opacity .1s ease-out;
  //transition-delay: 0s, 0s, 0s;
  //transition-delay: .1s;
  cursor: pointer;
  border: none;
  appearance: none;
  
  &:not([multiple]) {
    background-image: url(/wp-content/themes/spearmex/dist/images/dropdown.svg);
    background-position: 97% center;
    background-size: 24px 24px;
    background-repeat: no-repeat;
  }

  &:hover, &:focus {
    opacity: 1 !important;
    //box-shadow: 0 2px 5px 0 rgba(0,0,0,0.1),0 2px 10px 0 rgba(0,0,0,0.08);
    //transform: translate(0,2px);
    z-index: 999;
  }
}

.wpcf7 label, .wpcf7-form-control-wrap {
  width: 100%;
  max-width: 100%;
}

.wpcf7-form-control-wrap {
  input {
    width: 100%;
    max-width: 100%;
  }
}

input[type="email"], input[type="number"],
input[type="search"], input[type="tel"],
input[type="text"], input[type="date"],
input[type="url"], textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
  border-radius: 0;
  padding: 10px 25px;
  padding: .5555555556rem 1.3888888889rem;
  height: 50px;
  height: 2.7777777778rem;
  background-color: #f1f2f3;
  border: 0;
  box-sizing: border-box;
  color: #000;
  display: block;
  font-weight: 400;
  resize: none;
  vertical-align: middle;
  width: 100%;
}

.wpcf7-acceptance {
  input {
    width: auto;
    display: inline-block;
    vertical-align: middle;
  }
}

textarea {
  height: 140px;
  height: 8.75rem;
  border: none;
  border-radius: 0;
  background-color: #f1f2f3;
  resize: vertical;
}