.wrap {
    .content {
        padding: 3rem 0;

        .main {
            .page-header {
                text-align: center;
            }

            p {
                text-align: justify;
            }
        }
    }
}

.section-title, .section-bar {
    position: relative;

    &:after {
        content: "";
        display: block;
        width: 10%;
        height: 3px;
        background-color: #717171;
        margin-top: 1rem;
    }

    &.text-center {
        &:after {
            margin: 1rem auto 0;
        }
    }
}

.modal-video {
    background-color: #000;
}

.content {
    .text-block {
        max-width: 800px;
        margin: 10px auto 40px;
        text-align: center;
    }

    .woocommerce {
        margin-top: 32px;
    }
}

blockquote {
    &.wp-block-quote {
        width: 100%;
        height: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 2rem;
        background-color: #f6f6f6;
        margin: 40px 0;

        &:not(.is-large):not(.is-style-large) {
            border-left-color: #005d8f;
        }

        @include float();

        p {
            margin: 0 !important;
        }

        &.quote-button {
            p {
                font-size: 1.8rem;
            }
            a {
                @extend .btn;
                @extend .btn-add-to-cart;
                margin-bottom: 0 !important;
            }
        }
    }
}

.wp-block-embed-youtube {
    width: 100%;
    height: auto;
    position: relative;
    overflow: hidden;
    padding-top: 56.25%;
    @include float();

    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 0;
    }
}

.wp-block-gallery {
    &.object-foy-none {
        .blocks-gallery-item {
            flex-direction: initial !important;
        }
        img {
            object-fit: none !important;
        }
    }
    .blocks-gallery-item {
        img {
            max-width: 100%;
        }
    }
}

.wp-block-image {
    @media screen and (max-width: 575px) {
        max-width: 100%;
        width: 100% !important;
        height: auto;

        img {
            max-width: 100%;
            width: 100%;
            height: auto;
        }
    }
}