.btn {
    border-radius: 0;
}

.btn-outline {
    border: 1px solid;
}

.btn-buttons-menu {
    min-width: 200px;
    width: 100%;
}

.wp-block-button {
    & > a {
        @extend .btn;
    }
}

.btn-buttons-menu, .add_to_cart_button, .btn-add-to-cart, .form-submit .submit,
.single_add_to_cart_button, .coupon .button, [name="update_cart"], .checkout-button {
    display: inline-flex !important;
    color: #fff !important;
    border-radius: 3px !important;
    margin-bottom: 10px !important;
    margin-bottom: .5555555556rem !important;
    text-transform: uppercase;
    font-weight: 400 !important;
    overflow: hidden !important;
    letter-spacing: 1px;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: .8333333333rem !important;
    transform: translateZ(0);
    will-change: transform,opacity;
    transition: transform .3s,opacity .3s;
    justify-content: center;
    align-items: center;
    position: relative;

    .canopy-palette {
        background-color: map-get($canopy-palette, eighth) !important;

        &:before {
            background: map-get($canopy-palette, ninth);
        }

        &:after {
            border-color: map-get($canopy-palette, ninth);
        }
    }
    .spearmex-palette & {
        background-color: map-get($spearmex-palette, primary) !important;
        color: #222 !important;
        font-weight: 700 !important;
        transition: color 1s;

        &:hover, &:focus {
            color: #fff !important;
            transition: color 1s;
        }

        &:before {
            background: map-get($spearmex-palette, sixth);
        }

        &:after {
            border-color: map-get($spearmex-palette, sixth);
        }
    }

    &:not([disabled]) {
        &:hover, &:focus {
            color: #fff;

            &:after, &:before {
                opacity: 1;
                transform: scaleX(1);
            }
        }
    }

    &:after, &:before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        transition: transform .45s cubic-bezier(.645,.045,.355,1),opacity .45s cubic-bezier(.645,.045,.355,1);
        border-radius: 3px;
    }

    &:before {
        z-index: 0;
        left: 0;
        transform: scaleX(0);
        transform-origin: 50%;
        opacity: 1;
    }

    &:after {
        border-radius: 3px;
        z-index: 1;
        border: 1px solid;
        transform: scaleX(0);
        background-color: transparent;
        opacity: 0;
    }

    & > span {
        z-index: 4;
    }

    @media screen and (min-width: 960px) {
        padding: .8333333333rem 1.3888888889rem;
    }
}

.add_to_cart_button, .btn-add-to-cart, .form-submit .submit,
.single_add_to_cart_button, .coupon .button, [name="update_cart"], .checkout-button {
    @extend .btn;
}

.coupon .button, .button[name="update_cart"] {
    margin-bottom: 0 !important;
    padding: .7rem 1em !important;
}

.wp-block-button {
    .wp-block-button__link {
        border-radius: 4px !important;
    }
}

.wp-block-button__link {
    @extend .btn;
    
    &.has-background {
        transition: all .2s;
        
        &:hover, &:focus {
            opacity: .8;
            transition: all .2s;
        }

        &.has-pantone-108-c-background-color {
            background-color: $pantone-108c;
        }

        &.has-pantone-130-c-background-color {
            background-color: $pantone-130c;
        }
        
        &.has-pantone-1558-c-background-color {
            background-color: $pantone-1558c;
        }

        &.has-pantone-319-c-background-color {
            background-color: $pantone-319c;
        }

        &.has-pantone-298-c-background-color {
            background-color: $pantone-298c;
        }

        &.has-pantone-3005-c-background-color {
            background-color: $pantone-3005c;
        }
    }
}