.product .summary .product_meta .posted_in,
.product .summary .product_meta .tagged_as {display:none;}

.woocommerce {
    ul.products {
        li.product {
            background-color: #fff;
            @media screen and (max-width: 425px){
                width: 100% !important;           
            }
        }
    }
    .products {
        &.columns-4 {
            .add_to_cart_button {
                padding-left: 12px !important;
                padding-right: 12px !important;

                span {
                    font-size: 0;
                    text-align: center;

                    &:before {
                        font-family: "Font Awesome 5 Free";
                        font-weight: 900;
                        content: "\f217";
                        vertical-align: middle;
                        display: inline-block;
                        font-size: 1rem;
                        width: 100%;
                        height: auto;

                        font-style: normal;
                        font-variant: normal;
                        text-rendering: auto;
                        -webkit-font-smoothing: antialiased;
                    }
                }
            }
        }

        .product {
            height: auto;
            overflow-y: hidden;
            margin-top: 20px;
            padding-bottom: 5px !important;
            box-shadow: 0 8px 17px 0 rgba(0,0,0,0.12),0 6px 20px 0 rgba(0,0,0,0.1);
            border-radius: 2px;
            transition: box-shadow .2s ease-out,transform .2s ease-out,opacity .1s ease-out;
            transition-delay: 0s, 0s, 0s;
            transition-delay: .1s;
            margin-top: 25px;
            margin-bottom: 25px;

            &:hover, &:focus {
                opacity: 1 !important;
                box-shadow: 0 2px 5px 0 rgba(0,0,0,0.1),0 2px 10px 0 rgba(0,0,0,0.08);
                transform: translate(0,2px);
                z-index: 99;
            }

            .onsale {
                top: 10px !important;
                right: auto !important;
                left: 10px !important;
                margin: 0 !important;
                border-radius: 3px !important;
                min-height: auto !important;
                padding: 10px !important;
                line-height: 1 !important;
            }

            .star-rating {
                position: absolute;
                top: 10px;
                right: 10px;
                color: #e9d22a;
                z-index: 1;
            }

            .woocommerce-loop-product__link {

                & > img {
                    margin: 0;
                    max-height: 228px;
                    min-height: 228px;
                    object-fit: cover;
                }
            }

            .product-body {
                padding: 15px;

                .woocommerce-loop-product__title {
                    display: flex;
                    height: 50px;
                    margin-top: 0;
                    margin-bottom: 12px;
                    overflow: hidden;
                    font-size: 22px;
                    margin: 0;
                    padding: 0 !important;
                    width: 100%;
                    vertical-align: middle;
                    font-weight: 200 !important;
                    font-family: 'Proxima Nova', sans-serif !important;
                    color: #005989 !important;
                    height: 50px !important;
                }

                p {
                    color: #222;
                    overflow: hidden;
                    line-height: 1.4em;
                    height: 88px;
                    height: 5.5em !important;
                    position: relative;
                    letter-spacing: .2px;
                    margin-bottom: 0;

                    &:after {
                        content: "";
                        text-align: right;
                        position: absolute;
                        bottom: 0;
                        right: 0;
                        width: 70%;
                        height: 1.4em;
                        background: linear-gradient(to right,rgba(255,255,255,0),#fff 50%);
                    }
                }
            }

            .product-footer {
                //margin-top: 15px;
                padding: 15px;
                padding-top: 0;
                display: flex;
                justify-content: space-between;
                align-items: center;
                
                a {
                    display: flex;

                    &.add_to_cart_button {
                        padding: 15px 20px;
                        margin-bottom: 0 !important;
                        background-color: $green;
                    }

                    &.added {
                        &:after {
                            content: "" !important;
                        }
                    }

                    &.added_to_cart {
                        display: none;
                    }
                }

                .button, .price {
                    margin: 0;
                }

                .price {
                    font-weight: 700;
                    font-size: 18px;
                    //padding-left: 10px;
                    height: auto !important;
                    text-transform: none !important;
                    color: #222 !important;

                    .from {
                        font-size: .95rem;
                        text-transform: initial;
                        color: rgba(132, 132, 132, 0.69);

                        .br {
                            display: block;
                        }
                    }

                    del {
                        display: none;
                    }

                    ins {
                        text-decoration: none;
                    }
                }
            }
        }
    }

    .product {
        .summary.entry-summary {
            form.cart {
                .single_add_to_cart_button {
                    margin-bottom: 0 !important;
                    height: 50px;
                    height: 2.7777777778rem;
                }
            }
        }

        .onsale {
            top: 10px !important;
            right: auto !important;
            left: 10px !important;
            margin: 0 !important;
            border-radius: 3px !important;
            min-height: auto !important;
            padding: 10px !important;
            line-height: 1 !important;
        }
        .images {
            .flex-control-thumbs {
                li {
                    width: 10% !important;
                    &, & > img {
                        height: 51px;
                        object-fit: cover;
                    }
                    overflow: hidden;
                    &:nth-child(4n+1) {
                        clear: initial !important;
                    }
                }
            }
        }
    }

    .woocommerce-product-rating {
        .star-rating {
            color: #e9d22a;
        }
    }

    #reviews {
        .star-rating {
            color: #e9d22a;
        }
    }

    #review_form_wrapper {
        margin-top: 2rem;
        padding: 2rem;
        background-color: #fff;
        border: 1px solid #dfdfdf;
        //box-shadow: 0 8px 17px 0 rgba(0,0,0,.12),0 6px 20px 0 rgba(0,0,0,.1);
    }

    #review_form {
        .comment-reply-title {
            @extend .h3;
        }
        .comment-form {
            padding-top: 1rem;

            .product-review-comment {
                width: 100%;
                display: flex;
                flex-direction: row;
                
                .comment-form-rating {
                    max-width: 200px;
                    width: 100%;
                    text-align: right;
                    padding-right: 1rem;
                    .stars {
                        position: relative;
                        & > span {
                            position: absolute;
                            right: 0;
                            @extend .clearfix;
                        }
                        a {
                            color: #e9d22a;
                            &:focus, &:hover {
                                outline: none;
                            }
                        }
                    }
                }

                .comment-form-comment {
                    max-width: calc(100% - 200px);
                    width: 100%; // flex: 1 calc(100% - 200px);
                }
            }

            .form-submit {
                text-align: right !important;
            }
        }
    }
}

.woocommerce-checkout{
    span.woocommerce-input-wrapper{
        width: 100% !important;
    }
    span{
        &.select2{
            &.select2-container{
                &.select2-container--default{
                    width: 200px !important;
                }
            }
        } 
    }
}

.cart {
    .quantity {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        margin-right: .5rem !important;

        .minus, .plus {
            background-color: $pantone-627c;
            border: none;
            color: #fff;
            cursor: pointer;
            padding: 3px 6px;
        }

        .minus {
            border-top-left-radius: 3px;
            border-bottom-left-radius: 3px;
        }

        .plus {
            border-top-right-radius: 3px;
            border-bottom-right-radius: 3px;
        }

        .qty {
            appearance: textfield;
            width: 30px;
            background-color: $pantone-627c;
            color: #fff;
            border: none;
            padding-left: 0;
            padding-right: 0;
            text-align: center;

            &::-webkit-outer-spin-button,
            &::-webkit-inner-spin-button {
                display: none;
                appearance: none;
                margin: 0;
            }
        }
    }
}

.woocommerce-tabs {
    .tabs {
        text-align: center;
        margin-left: 0;
        padding-left: 0;
    }
}

.woocommerce-Tabs-panel--additional_information,
.woocommerce-Tabs-panel--description {
    & > h2 {
        margin-bottom: 2rem;
    }
}

.woocommerce-Reviews {
    .woocommerce-Reviews-title {
        margin-bottom: 2rem;
    }
}

.related {
    &.products {
        margin-top: 1rem;

        h2 {
            margin-bottom: 2rem;
        }

        .products {
            &.columns-4 {
                .has-post-thumbnail {
                    img {
                        min-height: 183px;
                    }
                }

                .product-body {
                    h2 {
                        font-size: 18px;
                    }
                }
                .product-footer a.add_to_cart_button {
                    padding: 12px 6px;
                    font-size: .68rem !important;
                    margin-bottom: 0 !important;
                }
            }
        }
    }
}

.woocommerce table.cart td.actions .input-text {
    width: 200px;
}

.shop_table {
    .cart_item {
        .product-remove {
            .remove {
                &:hover, &:focus {
                    line-height: .8;
                }
            }
        }
        .product-thumbnail {
            a {
                display: block;
                img {
                    max-width: 100px !important;
                    width: 100% !important;
                    height: auto !important;
                }
            }
        }
    }
}

.wc-proceed-to-checkout {
    .checkout-button {
        width: 100% !important;
        padding: 2rem 1em !important;
        margin-bottom: 0 !important;
    }
}

.widget_shopping_cart_content {
    .woocommerce-mini-cart__empty-message {
        margin-bottom: 0 !important;
        padding: 1rem;
        text-align: center;
    }
}

.details-adventure{
    .element-details{
        svg{
            width: 70px;
            height: 70px;
            fill: $green;
            display: block;
            will-change: transform;
            overflow: visible;

            &.icon-clock2 {
                stroke: $green;
            }
        }
        h4{
            color: $blue-canopy;
            font-weight: bold;
        }
        span{
            text-align: center;
            font-weight: bold;
            color: $blue-canopy;
        }
    }
}

.incluye-recomendacion{
    .incluye-section{
        ul{
            list-style: none;
            padding-left: 0;
            li{
                svg{
                    width: 30px;
                    height: 30px;
                    will-change: transform;
                    overflow: visible;
                }
            }
        }
    }
}

.horarios{
    .horario{
        span{
            display: block;
        }
    }
}