@font-face {
    font-family: 'Proxima Nova';
    src: local('Proxima Nova Regular'), local('ProximaNova-Regular'),
        url('/wp-content/themes/spearmex/dist/fonts/ProximaNova-Regular.woff2') format('woff2'),
        url('/wp-content/themes/spearmex/dist/fonts/ProximaNova-Regular.woff') format('woff'),
        url('/wp-content/themes/spearmex/dist/fonts/ProximaNova-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Myriad Pro';
    src: url('/wp-content/themes/spearmex/dist/fonts/MyriadPro-Light.eot');
    src: url('/wp-content/themes/spearmex/dist/fonts/MyriadPro-Light.eot#iefix') format('embedded-opentype'),
         url('/wp-content/themes/spearmex/dist/fonts/MyriadPro-Light.woff2') format('woff2'),
         url('/wp-content/themes/spearmex/dist/fonts/MyriadPro-Light.woff') format('woff'),
         url('/wp-content/themes/spearmex/dist/fonts/MyriadPro-Light.ttf') format('truetype'),
         url('/wp-content/themes/spearmex/dist/fonts/MyriadPro-Light.svg#MyriadPro-Light') format('svg');
    font-weight: 300;
    font-style: normal;
}

