// WordPress Generated Classes
// http://codex.wordpress.org/CSS#WordPress_Generated_Classes

// Media alignment
.alignnone {
  margin-left: 0;
  margin-right: 0;
  max-width: 100%;
  height: auto;
}
.aligncenter {
  display: block;
  margin: 1.5rem auto;
  height: auto;
}
.alignleft,
.alignright {
  /*margin-bottom: 1.5rem;
  margin-top: 1.5rem;*/
  height: auto;
}
.alignfull {
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
}
@include media-breakpoint-up(sm) {
  // Only float if not on an extra small device
  .alignleft {
    float: left;
    margin-right: 1.5rem;
  }
  .alignright {
    float: right;
    margin-left: 1.5rem;
  }
}

// Captions
.wp-caption {
  @extend .figure;
}
.wp-caption > img {
  @extend .figure-img;
  @extend .img-fluid;
}
.wp-caption-text {
  @extend .figure-caption;
}
.wp-caption-text {
  @extend .figure-caption;
}

// Text meant only for screen readers
.screen-reader-text {
  @extend .sr-only;
  @extend .sr-only-focusable;
}

.main {
  h1, .h1 {
    margin: 1.5rem 0;
  }
  h2, .h2 {
    margin: 1.3rem 0;
  }
  h3, h4, h5, h6, .h3, .h4, .h5, .h6 {
    margin: 1rem 0;
  }

  figure.wp-block-image {
    @include float();
  }
}