.page-404 {
    width: 100%;
    height: auto;
    margin-bottom: 3rem;

    .page-404-wrapper {
        width: 100%;
        height: auto;

        img.image-404 {
            max-width: 800px;
            width: 100%;
            height: auto;
            display: block;
            margin: 0 auto;
        }

        .text-404 {
            max-width: 500px;
            width: 100%;
            margin: 0 auto;
            text-align: center;

            p {
                text-align: center;
                font-size: 1.2rem;
            }

            &, & a {
                color: #5f3770;
            }

            .btn-back-home {
                border: 1px solid #5f3770;
                font-size: 1.2rem;
                margin: 1rem 0;
            }

            @extend %search-style;
        }
    }
}