.searched-term-not-fount {
    border: 1px solid #dadada;
    max-width: 800px;
    margin: 2rem auto 0;
    padding: 2rem;

    .alert {
        text-align: center;
    }

    @extend %search-style;
}

.search-results {
    .main {
        .page-header {
            margin-bottom: 1rem;
        }

        article {
            max-width: 800px;
            margin: 0 auto;

            a {
                text-decoration: none;
                color: #212529;
            }

            &:not(:last-child) {
                margin-bottom: 1rem;
                padding-bottom: 1rem;
                border-bottom: 1px solid #e2e2e2;
            }

            &.has-post-thumbnail {
                header {
                    .post-image {
                        width: 100%;
                        height: 300px;
                        position: relative;
                        overflow: hidden;
                        margin-bottom: .5rem;

                        img {
                            position: absolute;
                            width: 100%;
                            height: auto;
                            left: 0;
                            top: 50%;
                            transform: translateY(-50%);
                        }
                    }
                }
            }
        }

        .search-bottom, .search-top {
            margin-top: 2rem;
            @extend %search-style;
        }

        .search-top {
            margin-bottom: 2rem;
        }
    }
}