.single {
    .post {
        &.has-post-thumbnail {
            .post-image {
                width: 100%;
                height: auto;
                img {
                    max-width: 100%;
                    width: 100%;
                    height: auto;
                }
            }
        }

        header {
            background-color: #c4c6c7;
            color: #fff;
            text-align: center;

            .post-meta {
                padding: 1.5rem 1rem;

                .entry-title {
                    margin: 0;
                }

                .byline {
                    text-align: center;
                    margin: 0;
                }
            }
        }

        .entry-content {
            margin: 2rem 0;
            //border-bottom: 1px solid #ced4da;

            p, li {
                line-height: 1.8;
                font-size: 1.03rem;
            }
        }

        .navigation-post {
            margin-bottom: 1rem;
            display: flex;
            border: 1px solid #ced4da;
            border-right: 0;
            border-left: 0;

            .previous-link, .next-link {
                flex: 1;
                justify-self: center;
                padding: 1rem 0;

                a {
                    text-decoration: none;
                }
            }

            .previous-link {
                align-self: flex-start;
                text-align: left;
            }

            .next-link {
                align-self: flex-end;
                text-align: right;
            }
        }
    }
}

.blog, .archive {
    .content {
        .post {
            &:not(:first-child) {
                padding-top: 3rem;
            }

            &:not(:last-child) {
                border-bottom: 1px solid #c4c6c7;
                padding-bottom: 3rem;
            }

            .updated {
                margin: 0;
            }

            .entry-title {
                margin-top: 0;
                margin-bottom: 0;
            }

            .byline {
                font-size: .89rem;   
            }

            &.extended {
                img {
                    max-width: 100%;
                    height: auto;
                }
            }

            &.compact {
                .meta {
                    text-align: right;

                    .byline {
                        text-align: right !important;
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 1024px) {
    .canopy-whatsapp {
	display: none !important;
    }
}
 
.canopy-link {
    padding: 8px 20px 8px!important;
    color: white;
    font-size: 12px;
    border-radius: 2px;
    margin-right: 2px;
    cursor: pointer;
    -moz-background-clip: padding;
    -webkit-background-clip: padding-box;
    box-shadow: inset 0 -3px 0 rgba(0,0,0,.2);
    -moz-box-shadow: inset 0 -3px 0 rgba(0,0,0,.2);
    -webkit-box-shadow: inset 0 -3px 0 rgba(0,0,0,.2);
    margin-top: 2px;
    display: inline-block;
    text-decoration: none;
}
 
.canopy-link:hover,.canopy-link:active {
    color: white;
}
 
